import {
  WHITE,
  WHITE_PAWNS,
  BLACK,
  BLACK_PAWNS,
  AVAILABLE,
} from './chess.constant';

export default {
  onSelect(key, colIndex, rowIndex) {
    const col = this.pieces[key];
    // console.log(col, colIndex, rowIndex);

    if (col) {
      col.active = !col.active;
      col.key++;

      if (col.available) {
        if (col.icon) {
          if (col.color == 'white') {
            this.whitePieces.push(_.cloneDeep(col));
          } else {
            this.blackPieces.push(_.cloneDeep(col));
          }
        }

        Object.assign(col, this.activeCol);
        this.activeCol.icon = null;
        this.activeCol.active = false;
        this.activeCol.key++;

        this.activePlayer = this.activePlayer == 0 ? 1 : 0;

        // this.rows.forEach(row =>  {
        //   row.forEach(col =>  {
        //     col.available = false;
        //     col.key++;
        //   });
        // });
      } else if (col.color == this.players[this.activePlayer].color) {
        // this.rows.forEach(row =>  {
        //   row.forEach(col =>  {
        //     col.available = false;
        //     col.key++;
        //   });
        // });

        if (col.icon == 'knight') {
          this.availableKnight(col, rowIndex, colIndex);
        } else if (col.icon == 'pawn') {
          this.availablePawn(col, rowIndex, colIndex);
        } else {
          this.available(col, rowIndex, colIndex, AVAILABLE[col.icon]);
        }
      }

      this.activeCol = col;
      //   this.$forceUpdate();
    }
  },

  setRows() {
    this.rows = _.times(8).map(() => _.times(8).map(() => 'key'));

    this.history = [];
    this.activeBall = 1;
    this.activeColumn = null;

    this.rows[0] = WHITE;
    this.rows[1] = WHITE_PAWNS;
    this.rows[6] = BLACK_PAWNS;
    this.rows[7] = BLACK;

    this.copyRows = _.cloneDeep(this.rows);
  },

  setAvailable(col, rowIndex, colIndex, skip) {
    const key = this.rows[rowIndex] && this.rows[rowIndex][colIndex];
    const _col = this.pieces[key];
    // console.log(_col, col);

    if (_col) {
      if (_col.icon) {
        if (_col.color == col.color) {
          return true;
        } else {
          if (col.icon != 'pawn' || skip) {
            _col.available = true;
            _col.key++;
            return true;
          }
        }
      } else if (!skip) {
        _col.available = true;
        _col.key++;
      }
    }
  },

  availablePawn(col, rowIndex, colIndex) {
    // console.log(col, rowIndex, colIndex);

    if (col.color == 'white') {
      if (rowIndex == 1) {
        this.setAvailable(col, rowIndex + 2, colIndex);
      }

      this.setAvailable(col, rowIndex + 1, colIndex);
      this.setAvailable(col, rowIndex + 1, colIndex + 1, true);
      this.setAvailable(col, rowIndex + 1, colIndex - 1, true);
    } else {
      if (rowIndex == 6) {
        this.setAvailable(col, rowIndex - 2, colIndex);
      }

      this.setAvailable(col, rowIndex - 1, colIndex);
      this.setAvailable(col, rowIndex - 1, colIndex + 1, true);
      this.setAvailable(col, rowIndex - 1, colIndex - 1, true);
    }
  },

  availableKnight(col, rowIndex, colIndex) {
    this.setAvailable(col, rowIndex - 2, colIndex - 1);
    this.setAvailable(col, rowIndex - 2, colIndex + 1);
    this.setAvailable(col, rowIndex + 2, colIndex - 1);
    this.setAvailable(col, rowIndex + 2, colIndex + 1);
    this.setAvailable(col, rowIndex - 1, colIndex - 2);
    this.setAvailable(col, rowIndex - 1, colIndex + 2);
    this.setAvailable(col, rowIndex + 1, colIndex - 2);
    this.setAvailable(col, rowIndex + 1, colIndex + 2);
  },

  available(col, rowIndex, colIndex, available) {
    if (available) {
      if (available.topLeft) {
        for (let index = 1; index <= (available.move || colIndex); index++) {
          const end = this.setAvailable(
            col,
            rowIndex - index,
            colIndex - index
          );
          if (end) break;
        }
      }

      if (available.topRight) {
        for (
          let index = 1;
          index <= (available.move || 7 - colIndex);
          index++
        ) {
          const end = this.setAvailable(
            col,
            rowIndex - index,
            colIndex + index
          );
          if (end) break;
        }
      }

      if (available.bottomLeft) {
        for (let index = 1; index <= (available.move || colIndex); index++) {
          const end = this.setAvailable(
            col,
            rowIndex + index,
            colIndex - index
          );
          if (end) break;
        }
      }

      if (available.bottomRight) {
        for (
          let index = 1;
          index <= (available.move || 7 - colIndex);
          index++
        ) {
          const end = this.setAvailable(
            col,
            rowIndex + index,
            colIndex + index
          );
          if (end) break;
        }
      }

      if (available.top) {
        for (let index = 1; index <= (available.move || rowIndex); index++) {
          const end = this.setAvailable(col, rowIndex - index, colIndex);
          if (end) break;
        }
      }

      if (available.bottom) {
        for (
          let index = 1;
          index <= (available.move || 7 - rowIndex);
          index++
        ) {
          const end = this.setAvailable(col, rowIndex + index, colIndex);
          if (end) break;
        }
      }

      if (available.left) {
        for (let index = 1; index <= (available.move || colIndex); index++) {
          const end = this.setAvailable(col, rowIndex, colIndex - index);
          if (end) break;
        }
      }

      if (available.right) {
        for (
          let index = 1;
          index <= (available.move || 7 - colIndex);
          index++
        ) {
          const end = this.setAvailable(col, rowIndex, colIndex + index);
          if (end) break;
        }
      }
    }
  },
};
